<template>
  <div >
    <section id="header">
     <HeaderPublc></HeaderPublc>
    </section>
    <section id="cent">
      <div class="top">

      </div>
      <div class="center">
        <!--- --->
        <div class="saech" >
          <input class="saechIput"  v-model="input" :placeholder="languageData.public.sechText"></input>
          <el-button type="primary" class="saechBout"  icon="el-icon-search" v-on:click="saechFn()"> </el-button>
        </div>
        <div class="cardBox clearfix" :transport="transport"  v-loading="loading" :element-loading-text="languageData.public.loadingText" element-loading-spinner="el-icon-loading" element-loading-background="#f7f7f7">
          <div class="card clearfix"  v-for=" list in listData" v-on:click="goChart(list)">
            <div class="span4 device left clearfix">
              <div class=" textBox address clearfix">
                  <div class="left ctiy">{{languageData.id=='Chinese'?list.startPosition:list.startPositionEn}}</div>
                  <div class="left iconBox">
                    <div class="iocn">
                      <img :src="list.icon">
                    </div>
                    <div class="l"></div>
                    <p class="go">{{list.transportStatus==0?'运输中':list.transportStatus==1?'运输完成':list.transportStatus==2?'运输错误':'异常'}}</p>
                  </div>
                  <div class="left ctiy">{{languageData.id=='Chinese'?list.endPosition:list.endPositionEn}}</div>
                <div class="cleae"></div>
                <div class="text" >
                  <p class="goMap" style="cursor: pointer" @click.stop="gomap(list)"><i class="el-icon-location"></i> {{languageData.header.transport}}：{{list.now}}<i class="el-icon-arrow-right"></i></p>
                </div>
              </div>
            </div>
            <div class="span3 device left  clearfix">
              <div class="textBox deviceT">
                <p><label>{{languageData.public.plalte}}：</label><span>{{list.containerId}}</span></p>
                <p><label>{{languageData.public.ladingIdBill}}：</label><span>{{list.ladingId}}</span></p>
                <p><label>{{languageData.public.container}}：</label><span>{{list.deviceId}}</span></p>
              </div>
            </div>
            <div class="span3 device left clearfix" :mapListData="mapListData">
              <div class="deviceStatus textBox deviceT">
                <p><label>{{languageData.public.modeOfTransport}}： </label><span> {{languageData.id=='Chinese'?list.transportTypeText:list.transportTypeTextEn}} </span></p>
                <p><label>{{languageData.public.status}}： </label><span> {{list.transportStatus==0?'运输中':list.transportStatus==1?'运输完成':list.transportStatus==2?'运输错误':'异常'}}  </span></p>
                <p><label>{{languageData.public.deviceStatus}}： </label><span> {{languageData.id=='Chinese'?list.deviceOnfftext.CN:list.deviceOnfftext.EN}}  </span></p>
                <p><label>{{languageData.public.creaedTime}}： </label><span> {{list.ymd}} </span></p>
              </div>
            </div>
            <div class="cleae"></div>
            <div class="timeDate">
              <p class="year"> {{list.year?list.year:''}}</p>
              <p class="data" v-show="list.dateText">{{list.dateText}}</p>
              <p class="data" v-show="!list.dateText"><span > {{list.data}}</span>  <i class="month"> {{list.month}} {{languageData.public.month}}</i></p>
            </div>
            <div class="set">
              <div >
                <el-button round class="bot" :updateStatusData="updateStatusData" @click.stop="del(list.id)">{{languageData.public.Delete}}</el-button>
              </div>
              <div >
                <el-button round class="okBot" @click.stop="updatStatusFn(list.id)">{{languageData.public.completion}}</el-button>
              </div>
            </div>
          </div>
        </div>
        <div style="text-align: right;width: 70%;min-width: 980px; max-width: 1500px; padding-bottom: 40px;margin: auto" v-show="!loading">
          <el-pagination layout="prev, pager, next" :page-count="pages.cunt" v-show="pages.cunt>1" @current-change="handleCurrentChange">
          </el-pagination>
        </div>
      </div>
      <div class="botList">

      </div>
    </section>
    <section id="footer" style="text-align: right">
    </section>
  </div>
</template>
<style scoped>
  #cent{background-color: #f7f7f7}
  .saech{ width:567px;margin: auto;padding-top:45px; }
  .saechIput{width: 480px;margin-right: 0px;position: relative;z-index: 10;border: 1px solid #fff;height: 26px;line-height:26px;padding: 6px}
  .saechBout{border-radius: 0px 4px 4px 0px;position: relative; left: -7px;z-index: 11;font-size: 14px;font-weight: bold;width: 67px;text-align: center}
  .saechBout:hover{background-color: #409eff;border-color: #409eff}
  .saechBout{background-color: #0e72b9; border-color: #0e72b9 }
  .cardBox{width:70%;min-width: 980px;max-width:1500px;padding: 40px;margin: auto;position: relative;min-height: 400px;}
  .card{min-height: 140px;width: 100%;margin-top:20px;background-color: #fff;position: relative;cursor: pointer}
  .card:hover{box-shadow:0 0 16px #e2e7ea }
  .goMap:hover{box-shadow:0 0 16px #e2e7ea }
  .device { }
  .device p{ text-align: left;line-height: 34px;}
  .deviceStatus p{font-size: 12px;line-height: 28px;}
  .textBox{padding: 15px;margin: auto}
  .deviceT{width: 230px; }
  .address{ padding-left:30px;max-width:431px;min-width: 280px;margin-left: 0px;position: relative}
  .go{font-size: 12px;text-align: right ;text-indent:44px;}
  .ctiy{font-size: 28px;font-weight: bold;}
  .iconBox{width:100px;line-height: 40px;border-bottom: 2px solid #e1e1e1;position: relative;padding-right: 5px;padding-left: 5px;margin: 0px 15px;top: -15px;}
  .iocn{width: 25px;height:25px;background: #e1e1e1;border-radius:16px;  position: absolute;left: 20px;top:5px;}
  .iocn img { width: 100%;height: 100%}
  .l{width: 10px;height: 1px;border-bottom: 2px solid #e1e1e1;transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    -moz-transform:rotate(45deg);
      -webkit-transform:rotate(45deg);position: absolute;right: -2px;bottom:1px;}
  #cent input{ border: 0px none}
  .address .text{padding-top:40px;width: 90%;max-width: 300px;}
  .address .text p{padding-left:20px;background-color: #e1f3ff;font-size: 14px;line-height: 28px;height: 28px;}
  .timeDate{position: absolute;left: -90px;top: -50px;}
  .timeDate p{text-align: left;line-height: 40px;font-size: 18px;}
  .month{font-size: 14px;margin-left: 5px;}
   #cent .timeDate .data{font-size: 18px;}
  .year {height: 40px;width:90px;}
  .set{width: 104px;height:100px;position: absolute;right: -134px;top: 20px;}
  .bot{width: 104px;height: 40px;margin-bottom: 20px;border: 1px solid #fff}
  .okBot{background-color: #0e72b9; border-color: #0e72b9 ;color: #fff}
  .okBot:hover{background-color: #409eff;border-color: #409eff}
  .copy{padding: 5px 5px;margin-left: 40px; width: 40px;height: 20px;border: 1px solid #ccc; text-align: center;font-size: 12px;line-height: 20px;cursor: pointer}
  .update{color:#409eff;margin-left: 40px; text-align: center;font-size: 12px;line-height: 20px;cursor: pointer}
  #cent .ladingIdBill{border:1px solid #409eff;width: 150px;height: 25px;padding-left: 5px;}
</style>
<script>
  import HeaderPublc from '../public/herder.vue'
  import Public from '../../vuex/public.js'
  import jsonListData from '../../../static/local.json'
  import {mapState, mapActions} from 'vuex'
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
  import IconCarNor from '../../../static/img/icon/icon_car_nor.png'
  import IconCarSel from '../../../static/img/icon/icon_car_sel.png'
  import IconPlantNor from '../../../static/img/icon/icon_plant_nor.png'
  import IconPlantSel from '../../../static/img/icon/icon_plant_sel.png'
  import IconShipNor from '../../../static/img/icon/icon_ship_nor.png'
  import IconShipSel from '../../../static/img/icon/icon_ship_sel.png'
  import IconTrainNor from '../../../static/img/icon/icon_train_nor.png'
  import IconTrainSel from '../../../static/img/icon/icon_train_sel.png'
  export default {
    name: 'my-Chart',
    data () {
      return {
        input: null,
        year: null,
        pages: {
          page: 1,
          pageSize: 10,
          cunt: null
        },
        //
        list: [
          {
            id: 'AKDFAKJ2', // 柜号/车牌号
            startPosition: { // 起始地
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            endPosition: { // 目的地
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            transportStatus: 1, // 运输状态
            transportType: 2 // 运输类型
          },
          {
            id: 'AKDFAKJ2',
            startPosition: {
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            endPosition: {
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            transportStatus: 1,
            transportType: 2
          }
        ],
        // 渲染列表
        listData: [
//          {
//            id: 'AQS477254577',  // 柜号/车牌号
//            ladingId: 'AQS477254577', // 提货号
//            deviceId: 'AQS472547547', // 设备id
//            startPosition: '北京', // 起始地
//            endPosition: '圣地哥呀', // 目的地
//            transportStatus: 1, // 运输状态
//            transportType: 1, // 运输类型
//            deviceStatus: 0, // 设备状态
//            caretTime: null, // 创建时间
//            year: null, // 年
//            month: null, // 月
//            data: null, // 日
//            dateText: null, // 今天/昨天
//            now: '深圳南山码头', // 当前位置
//            transportTypeText: '飞机', // 运输渲染
//            icon: null // 图标
//          }
        ],
        loading: false
      }
    },
    methods: {
      ...mapActions({
        transportListpost: 'transportListFn',
        updateTansportStatuspost: 'updateTansportStatus',
        delPost: 'deletefn',
        checkID: 'checkId',
        mapListFn: 'mapListFn'
      }),
      // 分页
      handleCurrentChange (val) {
        this.pages.page = parseInt(val)
        if (this.input) {
          this.saechFn()
        } else {
          this.saechList()
        }
      },
      saechList () {
        this.loading = true
        var parm = {
          id: 1,
          page: this.pages.page,
          pageSize: this.pages.pageSize
        }
        this.transportListpost(parm)
      },
      // 查询按钮
      saechFn () {
        this.loading = true;
        if(this.input){
          var parm = {
            code: this.input
          }
          this.checkID(parm)
        } else {
           var parm = {id: 1}
          this.transportListpost(parm)
        }
      },
      // 列表解析
      listFn (res) {
        this.loading = false
        // 计算分页
        if (res.body.count) {
          var cunt = res.body.count
          // 取整
          this.pages.cunt = Math.ceil(cunt / this.pages.pageSize)
        }
        var res = res.body.data
        // 重置列表
        this.listData = []
        var listData = []
        for (let i in res) {
          // 提单号 ，运输状态描述，时间解析，今天/明天
          var ladingId, transportTypeText, time, dateText
          if (res[i].createTime) {
            time = Public.publicLo.timeCheng(res[i].createTime)
          } else {
            time = {ymd: null, hms: null, times: null, s: null, d: null, m: null, md: null, week: null, y: null}
          }
          // 当前时间
         var now = Public.publicLo.dataChange()
        // 匹配当前日期 和当前日期一样 赋值今天
          if ((now.y + now.m) == (time.y + time.m)) { // 匹配当前 年月
            var date1 = parseInt(now.d),
              date2 = parseInt(time.d)
            if (date1 == date2) { // 匹配当前日期
              dateText = '今天'
            } else if ((date1 - date2) == 1) {
              dateText = '昨天'
            }
          }
          // 拼接字符串
          transportTypeText = this.ransportSwitch(res[i].transportType)
          var strdata = jsonListData[res[i].startPosition.country]
          var endata = jsonListData[res[i].endPosition.country]
          var deviceOnfftext
          if (!strdata) {
            strdata = {
              CN: '',
              EN: ''
            }
          }
          if (!endata) {
            endata = {
              CN: '',
              EN: ''
            }
          }
//          if(strdata.length > 6){
//          }
//          if(endata.length > 6){
//          }
          if(res[i].deviceOnff && res[i].deviceOnff==1){
            deviceOnfftext = {
              CN: '开',
              EN: 'ON'
            }
          } else {
            if (res[i].deviceOnff == 0) {
              deviceOnfftext = {
                CN: '关',
                EN: 'OFF'
              }
            }else {
              deviceOnfftext = {
                CN: '异常',
                EN: 'error'
              }
            }
          }
          if (res[i].newAddress) {
            if (res[i].newAddress.length > 10) {
              res[i].newAddress = res[i].newAddress.substring(0,10) + '...'
            }
          } else {
            res[i].newAddress = '未请求到数据'
          }
          var list = {
            id: res[i].id,  // 后台唯一id
            containerId: res[i].containerId, // 柜号/车牌号
            ladingId: res[i].billNumber, // 提货号
            deviceId: res[i].deviceNumber, // 设备id
            deviceOnff: res[i].deviceOnff,
            deviceOnfftext: deviceOnfftext,
            startPosition: strdata.CN, // 起始地
            endPosition: endata.CN, // 目的地
            startPositionEn: strdata.EN, // 起始地英文
            endPositionEn: endata.EN, // 目的地英文
            transportStatus: res[i].transportStatus, // 运输状态
            transportType: res[i].transportType, // 运输类型
            deviceStatus: 0, // 设备状态
            time: time.hms, // 创建时间
            year: parseInt(time.y), // 年
            month: time.m, // 月
            data: time.d, // 日
            dateText: dateText, // 今天/昨天
            transportTypeText: transportTypeText.type,
            transportTypeTextEn: transportTypeText.typeEn,
            icon: transportTypeText.icon,
            now: res[i].newAddress, // 当前位置
            ymd: time.ymd + ' ' + time.hms
          }
          if (list.startPosition.length > 4) {
            list.startPosition = list.startPosition.substring(0, 4) + '...'
          }
          if (list.endPosition.length > 4) {
            list.endPosition = list.endPosition.substring(0, 4) + '...'
          }
          if (list.startPositionEn.length > 8) {
            list.startPositionEn = list.startPositionEn.substring(0, 8) + '...'
          }
          if (list.endPositionEn.length > 8) {
            list.endPositionEn = list.endPositionEn.substring(0, 8) + '...'
          }
          if (i == 0) {
            this.year = list.year
          } else {
            if (this.year == list.year) {
              list.year = ''
            }
          }
          listData.push(list)
        }
        this.listData = listData
      },
      // 解析运输方式
      ransportSwitch (data) {
        var transportTypeText
        switch (data) {
          case 0:
            transportTypeText = {type: '火车', icon: IconTrainNor, typeEn: 'Train'}
            break
          case 1:
            transportTypeText = {type: '汽车', icon: IconCarNor, typeEn: 'Motor'}
            break
          case 2:
            transportTypeText = {type: '轮船', icon: IconShipNor, typeEn: 'Freighter'}
            break
          case 3:
            transportTypeText = {type: '飞机', icon: IconPlantNor, typeEn: 'Flight'}
            break
          default:
            transportTypeText = { type: '异常', icon: IconCarNor }
            break
        }
        return transportTypeText
      },
      // 运输完成
      updatStatusFn (id) {
        var parm = {
          id: JSON.stringify(id),
          transportStatus: '1' // 0：运输中 1：运输完成 2：运输异常
        }
       this.updateTansportStatuspost(parm)
      },
      // 删除运输列表
      del (id) {
        this.loading = true
        var parm = {
          id: JSON.stringify(id)
        }
        this.delPost(parm)
      },
      // 运输列表操作
      listStatus (res) {
        this.loading = false
        if (res.status == 0) {
          // 修改成功
          this.openSuccess(res.message)
          if (this.input) {
           this.saechFn()
          } else {
           this.saechList()
          }
        }
        if (res.status != 0) {
          // 修改成功 修改失败
          this.openError(res.message)
        }
        // 清空 vuex 中的本次记录，以免被下次操作状态造成混淆
        this.$store.commit('updateStatusTo', {body: null})
      },
      // 跳转地图
      gomap (list) {
        // 清空 vuex 列表
        this.$store.commit('addMapList', null)
        this.$router.push({name: 'map', query: {id: list.id}})
        return false
      },
      // 查看详情
      goChart (list) {
        this.$router.push({name: 'chartSon', query: {id: list.id}})
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      }
    },
    computed: {
      transport: function () {
        var parm = this.transportList
        if (parm) {
          if(parm.body){
            this.listFn(parm)
          }
        }
        return 1
      },
      updateStatusData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
      mapListData: function () {
        var parm = this.mapList
        if (parm) {
        }
        return 1
      },
      languageData: function () {
        var parm = this.language
        if (parm) {
          if (parm.check == 'Chinese') {
            parm = parm.Chinese
          }
          if (parm.check == 'English') {
            parm = parm.English
//            this.formConfig.labelWidth = '200px'
//            dom.style = 'width:620px'
          }
          // 替换 其他交互数据
//          this.updateDate(parm)
        }
        return parm
      },
      ...mapState([
        'transportList',
        'updateStatus',
        'language',
        'mapList'
      ])
    },
    mounted () {
      // 配置顶部菜单
//      var parm = {logo: true, login: false, us: true, language: false, user: true, nav: true}
//      this.$store.commit('updateNave', parm)
      this.saechList()
    },
    components: {
      ElButton,
      HeaderPublc
    }
  }
</script>




